import React, { Component, Suspense } from 'react';
import { toast } from "react-toastify";
import { decode as base64_decode, encode as base64_encode } from 'base-64';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import { COPY_TO_CLIPBOARD, windowOpen } from '../../../Services/Utils/utils';
import { HOST_URL, EmbedURL } from '../../../Services/Api';
import { mailSendRequest } from '../../../Services/Actions/mailActions'
const ShareEmbedModal = React.lazy(() => import('../../../Layouts/Main/PublishCommonComponents/ShareEmbedModal'));

class HashtagCampaign extends Component {
    constructor(props) {
        super(props);
        this.state = { showModal: false, genrateCode: false, codeCopied: false, shareLink: "", isLoading: false };
    }
    shareEmbedModal = event => this.setState({
        showModal: true
    })
    shareEmbedCallback = (shareEmbedEvent) => {
        this.setState({
            showModal: shareEmbedEvent
        })
    }
    EditCampaignUrlModal = event => this.setState({
        showModal: true
    })
    campaignUrlCallback = (campaignUrlEvent) => {
        this.setState({
            showModal: campaignUrlEvent
        })
    }
    codeCopy = (id) => event => {
        this.setState({
            codeCopied: true
        })
        setTimeout(() => {
            this.setState({
                codeCopied: false
            })
        }, 1500);
        COPY_TO_CLIPBOARD(id);
    }
    onClickToShare = (mailId) => event => {
        const { responseData } = this.props
        var self = this;
        this.setState({ isLoading: true })
        if (mailId && String(mailId).length > 0) {
            mailSendRequest({ htmlCode: (responseData.publishCode && Object.keys(responseData.publishCode).length > 0) ? `${EmbedURL}${responseData.publishCode.wallId}?ugcSuite=1&hashtag_campaign=true` : null, email: mailId, type: 2 }).then((response) => {
                toast.success("Tagbox widget code successfully sent.")
                self.setState({ showModal: false, isLoading: false })
            })
        }
        else {
            toast.error("Please enter email id.")
        }
    }
    render() {
        const { genrateCode, showModal, codeCopied } = this.state
        const { responseData } = this.props

        var shareLink = ""

        if ((responseData.publishCode && Object.keys(responseData.publishCode).length > 0)) {
            const WallId = responseData.publishCode.wallId;
            const timeStemp = new Date() / 1000 | 0;
            shareLink = (base64_encode(`${responseData.publishCode.name}---${WallId}&${timeStemp}`))
        }

        return <>
            <div className="d-flex flex-column flex-shrink-0 emailcapaignPreview bg-light p-5">
                <div className="">
                    <h4 className="mb-2 fs-6">Publish Hashtag Content Anywhere Your Audience Lies!</h4>
                    <h5 className="mb-1 fs-7">1. Social Share</h5>
                    <p>Use this URL on your social networks to attract followers and encourage them to share their experiences. </p>

                    <div className="hashtag_social_share d-flex align-items-center justify-content-start bg-white border rounded-1 px-3 py-2 mb-3">
                        <div className="hashtag_social_share_inner d-flex align-items-center">
                            <button type="button" className="btn btn-icon rounded-circle h-30px w-30px p-1 mx-1" data-type="facebook" onClick={(e) => windowOpen(`https://www.facebook.com/sharer.php?u=${HOST_URL}share/hashtagcampaign/${shareLink}`, 'sharer', 'toolbar=0,status=0,width=600,height=400')}>
                                <i className="ri-facebook-circle-fill icon-4 text-light-alt"></i>
                            </button>
                            <button type="button" className="btn btn-icon rounded-circle h-30px w-30px p-1 mx-1" data-type="twitter" onClick={(e) => windowOpen(`http://twitter.com/share?url=${HOST_URL}share/hashtagcampaign/${shareLink}`, 'sharer', 'toolbar=0,status=0,width=600,height=400')}>
                                <i className="ri-twitter-fill icon-4 text-light-alt"></i>
                            </button>
                            <button type="button" className="btn btn-icon rounded-circle h-30px w-30px p-1 mx-1" data-type="linkedin" onClick={(e) => windowOpen(`https://www.linkedin.com/shareArticle?mini=true&url=${HOST_URL}share/hashtagcampaign/${shareLink}`, 'sharer', 'toolbar=0,status=0,width=600,height=400')}>
                                <i className="ri-linkedin-fill icon-4 text-light-alt"></i>
                            </button>
                            <button type="button" className="btn btn-icon rounded-circle h-30px w-30px p-1 mx-1 share-hashtag-campaign-url" data-type="envelope" onClick={this.shareEmbedModal}>
                                <i className="ri-mail-fill icon-4 text-light-alt"></i>
                            </button>
                        </div>
                    </div>
                    <h5 className="mb-1 fs-7">2. Emails</h5>
                    <p>Add hashtag content to your emailers and make them more personalized. Prompt your subscribers to share more content as well with your campaign.  </p>
                    <h5 className="mb-1 fs-7">3. SMS/WhatsApp</h5>
                    <p>Incorporate hashtag campaign URL in SMS or WhatsApp marketing to grab user attention and invite them to participate. </p>
                    <div className="position-relative">
                        <textarea className="form-control textarea-min-row-9 p-lg-4" id="hashtagUrlMsgCode_" onClick={this.codeCopy('hashtagUrlMsgCode_')}>

                            {`Thank you for trusting us. We love our customers, and your confidence in us means a lot to us. We hope we served you well, and we’d love your feedback. Click on the link below and tell us what you think! Share pictures, videos, or text, and we’d love to feature you on our socials.  ${(responseData.publishCode && Object.keys(responseData.publishCode).length > 0) ? `${EmbedURL}${responseData.publishCode.wallId}?ugcSuite=1` : null}`}


                        </textarea>
                        <div className="emebed_action position-absolute pe-2 pb-2" style={{ right: 5, bottom: 5 }}>
                            <OverlayTrigger placement="top" overlay={<Tooltip>{!codeCopied ? 'Copy Code' : 'Code Copied'}</Tooltip>}>
                                <button type="button" className="btn text-gray-500 p-0 me-2" onClick={this.codeCopy('hashtagUrlMsgCode_')}>
                                    {!codeCopied ?
                                        <i className="ri-file-copy-fill icon-6"></i>
                                        :
                                        <i className="ri-check-line icon-6 text-success"></i>
                                    }
                                </button>
                            </OverlayTrigger>
                        </div>
                    </div>
                </div>
            </div>
            <Suspense fallback={<></>}>
                {showModal ? <ShareEmbedModal shareEmbedCallback={this.shareEmbedCallback} shareEmbedModal={showModal} onClickToShare={this.onClickToShare} isLoading={this.state.isLoading} text="Share this embed code with your team. Enter the email Id to send the code directly to them" heading="Send code by email" /> : null}
            </Suspense>
        </>
    }
}

export default HashtagCampaign;